<script>
export default {
  name: "Home",
  data() {
    return {
      features: [
        {
          title: "Personalized Fitness Plans",
          description:
            "Get workout plans designed for your unique goals and physical condition.",
          buttonText: "Generate Plan",
          icon: "fas fa-running",
          action: this.generateFitnessPlan,
        },
        {
          title: "Nutritional Guidelines",
          description:
            "Complement your fitness journey with personalized diet plans.",
          buttonText: "View Nutrition",
          icon: "fas fa-utensils",
          action: this.viewNutritionPlan,
        },
        {
          title: "Progress Tracking",
          description:
            "Monitor your progress with intuitive and visual feedback tools.",
          buttonText: "Track Progress",
          icon: "fas fa-chart-pie",
          action: this.viewProgress,
        },
        {
          title: "Community Support",
          description:
            "Connect with like-minded individuals and share your fitness journey.",
          buttonText: "Join Community",
          icon: "fas fa-users",
          action: this.joinCommunity,
        },
      ],
    };
  },
  methods: {
    startJourney() {
      alert("Welcome to your fitness journey!");
    },
    generateFitnessPlan() {
      alert("Generating your personalized fitness plan...");
    },
    viewNutritionPlan() {
      alert("Viewing your nutritional guidelines...");
    },
    viewProgress() {
      alert("Viewing your progress tracking...");
    },
    joinCommunity() {
      alert("Joining the community...");
    },
    logout() {
      // 清除登录状态
      localStorage.removeItem("isAuthenticated");
      localStorage.removeItem("userRole");
      this.$router.push({ path: "/" });
      this.$message({
        message: "You have been logged out.",
        type: "success",
      });
    },
  },
};
</script>

